import {
  Box,
  Button,
  Container,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Stack,
  TextField,
  Typography,
} from "@mui/material";


import React, { useEffect, useState } from "react";
import { useRef } from "react";
import Layout from "../../layouts";
import { enqueueSnackbar } from "notistack";
import { ethers } from 'ethers';



const VARIANT_TYPE = ["default", "info", "success", "warning", "error"];

export default function HomePage() {
  const [errorMessage, setErrorMessage] = useState(null);
  const [defaultAccount, setDefaultAccount] = useState(null);
  const [userBalance, setUserBalance] = useState(null);
  const [signer, setSigner] = useState(null);
  const [provider, setProvider] = useState(null);
  let firstBalance  = true;

  const onConnect =async () => {

    if (window.ethereum == null) {
        console.log("MetaMask not installed; using read-only defaults")
        provider = ethers.getDefaultProvider()
    
    } else {
        let provider = new ethers.BrowserProvider(window.ethereum)
        let signer = await provider.getSigner();
        setSigner(signer);
        setProvider(provider);
        console.log("siger",signer);
        enqueueSnackbar(`connected to ${signer.address}`);
        getuserBalance();
    }
  };


  const getuserBalance = async () => {
    if (provider != null ){
      
      let balance = await provider.getBalance(signer.address, "latest")
      let prettyBalance= ethers.formatEther(balance);
      if (firstBalance) {
        firstBalance = false;
        setUserBalance(prettyBalance);
        return ;
      }
      console.log("balance:", userBalance);
      if (userBalance != prettyBalance) { 
        setUserBalance(prettyBalance);
        enqueueSnackbar(`balance for   ${signer.address} is ${prettyBalance}`);
        let d = prettyBalance-userBalance;
        let synth = window.speechSynthesis;
        let msg = `You Got ${d} ETH`;
        let utterThis = new SpeechSynthesisUtterance(msg);
        synth.speak(utterThis);
      }
    }
  }

  const headerButton = (
    <Button
      variant={"contained"}
      color="secondary"
      onClick={() => onConnect()}
    >
    {signer?signer.address:"Connect" }   
    </Button>
  );

  useEffect(()=>{
    const timer = setInterval(()=>{
      getuserBalance();
    }, 1000);
    return () => clearInterval(timer);
  });

  return (
    <Layout customHeaderButton={headerButton}>
      <Box
        sx={{
          bgcolor: "background.paper",
          pt: 8,
          pb: 6,
        }}
      >
        <Container maxWidth="sm">
          <Typography
            component="h1"
            variant="h2"
            align="center"
            color="text.primary"
            gutterBottom
          >
           Wallet Watcher 
          </Typography>

          <Stack
            sx={{ pt: 4 }}
            direction="row"
            spacing={2}
            justifyContent="center"
          >

          </Stack>
        </Container>
      </Box>
    </Layout>
  );
}
